@media (min-width: 768px) {
  .card__container {
    margin-top: 50px;
    width: 700px;
    height: 450px;
    display: flex;
    background-color: rgb(83, 83, 83);
    border: 5px solid rgb(255, 255, 255);
    border-radius: 5px;
    box-shadow: 10px 10px 36px 0px rgba(0,0,0,0.85);
  }
}

@media (min-width: 320px) {
  .card__container {
    margin-top: 50px;
    width: 300px;
    height: 750px;
    display: flex;
    flex-direction: column;
    background-color: rgb(83, 83, 83);
    border: 5px solid rgb(255, 255, 255);
    border-radius: 5px;
    box-shadow: 10px 10px 36px 0px rgba(0,0,0,0.85);
  }
}

.info__container {
  padding: 20px;
  color: rgb(255, 255, 255);
}

.image__card {
  height: 450px;
  width: 300px;
  background-image: cover;
  background-size: cover;
}

.name__card {
  text-align: center;
}

.house__card {
  padding-top: 70px;
}

.patronus__card {
  text-transform: capitalize;
}

.fa-heartbeat {
  padding-left: 10px;
  font-size: 26px;
  color: rgb(197, 0, 0);
}

.fa-skull {
  padding-left: 10px;
  font-size: 26px;
  color: rgb(255, 255, 255);
}

.return__link {
  margin-top: 25px;
  padding: 5px 15px 5px 15px;
  border: 5px solid rgb(255, 255, 255);
  border-radius: 5px;
  background-color: rgb(226, 192, 0);
  text-decoration: none;
  font-weight: bold;
  font-size: 20px;
  color: rgb(255, 255, 255);
}

.return__link:hover {
  transform: scale(1.1);
  box-shadow: 10px 10px 36px 0px rgba(0,0,0,0.85);
}
